import React from 'react'
import MovieItem from './MovieItem';
import {API_URL, API_KEY_V3} from '../utills/api';
import MovieTabs from './MovieTabs';
import Pagination from './Pagination';


class App extends React.Component{

    constructor(){
        super();

        this.state = {
            movies :[],
            moviesWillWatch: [],
            sort_by: "popularity.desc",
            pageNumber: 1,
            totalPages: 0
        }
    }


    componentDidMount(){
      this.getMovies();
    }

    componentDidUpdate(prevProps,PrevState){
        if (PrevState.sort_by !== this.state.sort_by){
           this.getMovies();
         // console.log('!!!!')
        }

        if (PrevState.pageNumber !== this.state.pageNumber){
            this.getMovies();
        }
    }


    getMovies = () =>{
        fetch(`${API_URL}/discover/movie?api_key=${API_KEY_V3}&sort_by=${this.state.sort_by}&page=${this.state.pageNumber}`)
        
        .then((res)=>{
            return res.json();
         }).then((data)=>{
             this.setState({
                 movies: data.results,
                 totalPages : data.totalPages
             })
         });
    }

    removeMovie = movie =>{
        const updateMovies = this.state.movies.filter( (el)=> el.id !== movie.id);
        this.setState({
            movies : updateMovies
        })
    
    }

    addMovieToWillWatch = movie =>{

        const updateMovies = [...this.state.moviesWillWatch, movie];
        this.setState({
            moviesWillWatch:updateMovies
        })
    }

    removeMovieFromWillWatch = movie =>{
        const updateMoviesWillWatch = this.state.moviesWillWatch.filter( (el)=> el.id !== movie.id);
        this.setState({
           moviesWillWatch : updateMoviesWillWatch
        })
    
    }


    updateSortBy = value =>{
        this.setState({
            sort_by: value
        })
    }
    
    goToPage = pageNum =>{
        this.setState({
            pageNumber: pageNum
        })
       
    }

    goToNextPage = () =>{
        let prevPageNumber = this.state.pageNumber;
      
            this.setState({
                pageNumber : ++prevPageNumber
            })
        
    }

    goToPrevPage = () =>{
        let prevPageNumber = this.state.pageNumber;

            this.setState({
                pageNumber : --prevPageNumber
            })
           
        
    }


    render(){  
        return (<div className = "container mt-4">

                    <div className = "row">
                        <div className = "col-9">

                            <div className="row mb-4">

                                <div className="col-12">
                                    <MovieTabs sort_by={this.state.sort_by} updateSortBy= {this.updateSortBy}/>
                                </div>
                               
                            </div>

                            <div className = "row">
                                {this.state.movies.map((movie)=> {
                                    return( 
                                        <div className ="col-6 mb-4"  key = {movie.id}>
                                            <MovieItem movie = {movie} removeMovie = {this.removeMovie}
                                                 addMovieToWillWatch = {this.addMovieToWillWatch}
                                                 removeMovieFromWillWatch = {this.removeMovieFromWillWatch}
                                            />
                                        </div>
                                        )})}  
                            </div>
                        </div>

                        <div className = "col-3">
                            <p>Will Watch : {this.state.moviesWillWatch.length}</p>
                        </div>
                    </div>

                    <div className="row">
                        <div className="col-3">
                                <Pagination pageNumber = {this.state.pageNumber} goToPage = {this.goToPage}
                                     goToNextPage = {this.goToNextPage} goToPrevPage = {this.goToPrevPage}
                                     totalPages = {this.state.totalPages}
                                />
                        </div>
                    </div>

                 </div>)
    }
}


export default App;