import React from 'react';

class Pagination extends React.Component{

 

    render(){

        const {pageNumber, goToPage ,goToNextPage ,goToPrevPage, totalPages} = this.props;
        const goPageByNum = value => { return ()=> goToPage(value)};
        const getClassLink = value => { return `page-item ${pageNumber === value ? "active" : "" }`};
        const goToNextPg = (pageNumber) => { if (pageNumber <=5)  return () => goToNextPage()};
        const goToPrevPg = (pageNumber) => { if (pageNumber >=2)return () => goToPrevPage()};
        return (
        <ul className="pag pagination">
            <li className={ pageNumber === 1 ? "page-item disabled" : "page-item"}>
                <div className = "page-link" /* aria-disabled="true"*/ onClick = {goToPrevPg(pageNumber)}>Previos</div>
            </li>
            <li className= {getClassLink(1)}>
                <div className="page-link" onClick = {goPageByNum(1)}>1</div>
            </li>
            <li className={getClassLink(2)}>
                <div className="page-link" onClick = {goPageByNum(2)}>2</div>
            </li>
            <li className={getClassLink(3)}>
                <div className="page-link" onClick = {goPageByNum(3)}>3</div>
            </li>
            <li className={getClassLink(4)}>
                <div className="page-link" onClick = {goPageByNum(4)}>4</div>
            </li>
            <li className={getClassLink(5)}>
                <div className="page-link" onClick = {goPageByNum(5)}>5</div>
            </li>
            <li className={ pageNumber === 5 ? "page-item disabled" : "page-item"}> 
                <div className = "page-link" onClick = {goToNextPg(pageNumber)}>Next</div>
            </li>
            
        </ul>
        )
    }
}


export default Pagination;