import React from 'react';

const URL = "https://image.tmdb.org/t/p/w500";

class movieItem extends React.Component{
    constructor(){
        super();

        this.state = {
            WillWatch : false
        }

    }

    componentWillUnmount(){
        
    }


    render(){
        const {movie, removeMovie,  addMovieToWillWatch, removeMovieFromWillWatch} = this.props;

        
            return (
            <div className="card">
        <img
            className="card-img-top"
            src={`https://image.tmdb.org/t/p/w500${movie.backdrop_path}`}
            alt=""
        />
        <div className="card-body">
            <h6 className="card-title">{movie.title}</h6>
            <div className="d-flex flex-column">
            <p className="mb-4">Rating: {movie.vote_average}</p>
            <div className="d-flex justify-content-between">

                
                    {this.state.WillWatch === true ? 
                        <button type="button" className="btn btn-success" onClick = {
                            ()=>{
                                this.setState({
                                    WillWatch : false
                                });
                                removeMovieFromWillWatch(movie);
                            }
                        }>
                        Remove Will Watch
                        </button> :
                        <button type="button" className="btn btn-secondary" onClick ={ () => {
                            this.setState({
                                WillWatch : true
                            });
                            addMovieToWillWatch(movie)}}>
                    Add Will Watch
                        </button>
                    }
            
           
            <button type= "button" className ="btn btn-outline-danger" onClick = {removeMovie.bind(null, movie)}>Delete movie</button>
            </div>
        </div>
        </div>
        </div>
            
            )

    }
}


export default movieItem;